@mixin nav_active {
  position: relative;
  &::after {
    opacity: 0;
    content: '';
    position: absolute;
    bottom: -0.4em;
    left: 0.4em;
    right: 0.4em;
    height: 0.15em;
    background-color: var(--themeColor);
  }

  &.on {
    font-weight: bold;
    &::after {
      opacity: 1;
    }
  }
}

@mixin ellipse($line: 2) {
  display: -webkit-box;
  /*! autoprefixer: ignore next */
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $line;
  overflow: hidden;
}

@mixin flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin select-nav($width: 80px) {
  .nav {
    flex: none;
    border-radius: 15px;
    display: flex;
    border: 1px solid #BBBBBB;
    margin: 10px auto;
    width: min-content;

    &_link {
      position: relative;
      flex: none;
      width: $width;
      height: 30px;
      line-height: 30px;
      text-align: center;
      border-radius: 15px;

      &:hover {
        background-color: #F5F5F5;
      }

      &.on {
        background-color: #BBBBBB;
        color: #fff;
      }

      .lt_ico {
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(20%, -50%);
        border: 1px solid #fff;
        border-radius: 1000px;
        background-color: var(--themeColor);
        line-height: 1;
        padding: 2px 4px;
        color: #fff;
      }
    }
  }
}

.domSearch {
  &_multimatch {
    .item {
      width: 366px;
    }
  }

  &_main {
    ._list {

      .item {
        padding: 10px 30px;
        grid-template-columns: var(--grid-template-columns);
        .cover {
          width: 60px;
          height: 60px;
          border-radius: 4px;
          overflow: hidden;
          border: 1px solid #ccc;
        }
      }
    }

    .lyrics_list {

      .lrc {
        grid-column: 4 / 9;

        .content {
          word-break: break-all;
          white-space: pre-line;
          &::first-line {
            // margin-top: 0.2em;
            margin-bottom: 0.2em;
            font-size: larger;
          }
          b {
            color: var(--linkColor);
            font-weight: normal;
          }
        }
      }
    }

    .userprofiles_list {
      .item {
        .cover {
          border-radius: 50%;
        }
      }
    }
  }
}
